import DcTypeService from '@/services/trade/DcTypeService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let DcTypeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultDcTypeTableFilter: {
      type: String,
      default: null
    },
    defaultDcTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      dcTypeTableData: [],
      dcTypeTableFilterTimer: null,
      dcTypeTableFilter: this.defaultDcTypeTableFilter,
      dcTypeTableOrderBy: this.defaultDcTypeTableOrderBy,
      dcTypeTableContainer: 'loading-container',
      dcTypeTableBusy: false,
      dcTypeTableTotalRows: 0,
      dcTypeTableCurrentPage: 1,
      dcTypeTablePerPage: 10
    }
  },
  computed: {
    dcTypeTableNoDataText: function () {
      return this.dcTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    dcTypeTableTotalPages: function () {
      return Math.ceil(this.dcTypeTableTotalRows / this.dcTypeTablePerPage)
    },
    dcTypeTablePagingInfo: function () {
      return this.pagingInfo(this.dcTypeTableCurrentPage, this.dcTypeTablePerPage, this.dcTypeTableTotalRows)
    }
  },
  methods: {
    loadDcTypeTableData (loadingContainerId) {
      let self = this
      this.dcTypeTableBusy = true
      this.dcTypeTableContainer = loadingContainerId || this.dcTypeTableContainer
      this.startLoading(this.dcTypeTableContainer)
      DcTypeService.dcTypes({
        pageIndex: this.dcTypeTableCurrentPage - 1,
        pageSize: this.dcTypeTablePerPage,
        filter: this.dcTypeTableFilter,
        orderByField: this.dcTypeTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.dcTypeTableContainer)
        self.dcTypeTableBusy = false
        self.dcTypeTableTotalRows = response.data.totalRows
        self.dcTypeTableData = response.data.dcTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.dcTypeTableContainer)
        self.dcTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshDcTypeTable () {
      this.loadDcTypeTableData()
    },
    filterDcTypeTable (filter) {
      let self = this
      if (this.dcTypeTableFilter !== filter) {
        clearTimeout(this.dcTypeTableFilterTimer)
        this.dcTypeTableFilterTimer = setTimeout(() => {
          self.dcTypeTableFilter = filter
          self.loadDcTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.dcTypeTableFilterTimer)
      }
    },
    orderDcTypeTable (orderBy, active) {
      this.dcTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadDcTypeTableData()
    }
  },
  watch: {
    dcTypeTableCurrentPage: function () {
      this.refreshDcTypeTable()
    },
    dcTypeTablePerPage: function () {
      this.refreshDcTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshDcTypeTable()
    }
  }
}

export default DcTypeTableMixin
